.service_uls{
  transition: visibility 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
  transition: transform 300ms ease-in-out, visibility 300ms ease-in-out;
  transition: transform 300ms ease-in-out, visibility 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
}
#page_right[data-state="first"]{
  visibility: visible;
}
#page_right[data-state="second"]{
  visibility: hidden;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
#page_right[data-state="third"]{
  visibility: hidden;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

#page_middle[data-state="first"]{
  -webkit-transform: translateX(0);
          transform: translateX(0);
  visibility: hidden;
}

#page_middle[data-state="second"]{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  visibility: visible;
}
#page_middle[data-state="third"]{
  -webkit-transform: translateX(-200%);
          transform: translateX(-200%);
  visibility: visible;
}
#page_left[data-state="first"]{
  visibility: hidden;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
#page_left[data-state="second"]{
  visibility: hidden;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
#page_left[data-state="third"]{
